@tailwind base;
@tailwind components;
@tailwind utilities;


*, *::after,*::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background: rgb(250, 250, 250);
}

.expand{
  display: none;
   transition: display 200ms ease;
 
 }
 
 .rotate {
  transform: rotate(-180deg);
  transition: transform 400ms ease;
}
.rotate-alt {
  transform: rotate(0deg);
  transition: transform 400ms ease;
}





.before-box::before{
content:"";
background: #1C1943 ;
height:100%;
width:100%;
z-index:-99;
position: absolute;
}






.filehide{
  display:none;
}
.table{
  display: block;
    white-space: nowrap;
  }

.borderradiusformtop{
  border-radius: 10px 10px 0 0;
}
.sidebar ul li:hover span{
    margin-left: 5px;
}
